import React, {useEffect} from "react"
import "./EmojiList.css";
import emojiUnicode from "emoji-unicode"
import Clipboard from "clipboard";

function EmojiList(props) {

    useEffect(() => {
        const clipboard = new Clipboard(".copy-to-clipboard");
        
        return () => clipboard.destroy()
    }, []);

    function changeText(event) {
        if (event.target.classList.contains("emoji-container")) {
            event.target.children[2].innerText = "Copied!"
        }else if (event.target.classList.contains("emoji-title")) {
            event.target.nextSibling.innerText = "Copied!"
        }else if (event.target.localName === "img") {
            event.target.nextSibling.nextSibling.innerText = "Copied!"
        }else {
            event.target.innerText = "Copied!"
        }
    }

    function resetText(event) {
        if (event.target.classList.contains("emoji-container")) {
            event.target.children[2].innerText = "Click to copy"
        }else {
            return null
        }
    }

    if (props.emojiData.length === 0) {
        return (
            <section>
                <div className="error-msg">‚ùå No data found</div>
            </section>
        )
    }

    return (
        <section>
            {props.emojiData.map(emoji => (
                <div className="emoji-container copy-to-clipboard" key={emoji.title} data-clipboard-text={emoji.symbol} onClick={changeText} onMouseLeave={resetText}>
                    <img alt={props.title} src={`//cdn.jsdelivr.net/emojione/assets/png/${emojiUnicode(emoji.symbol)}.png`} />
                    <span className="emoji-title">{emoji.title}</span>
                    <span className="emoji-clipboard">Click to copy</span>
                </div>
            ))}
        </section>
    )
}

export default EmojiList